footer.footer {
  padding-top: 4rem;
  padding-bottom: 4rem;
  color: #FFFFFF;
}
footer.footer li > a {
  color: #eb5757;
}

.showcase .links-img {
  background-color: #f7f7f7;
  box-shadow: 0 7px 14px rgba(50, 50, 93, 0.1), 0 3px 6px rgba(0, 0, 0, 0.08);
  padding-left: 0.5rem;
  padding-top: 0.2rem;
  padding-bottom: 0.2rem;
  margin-bottom: 1rem;
  border-radius: 5px;
}
.showcase .links-img .link {
  text-decoration: none;
}
.showcase .links-img .link p {
  font-size: 1rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  padding-top: 0.5rem;
  color: #eb5757;
}
.showcase .links-img .link :hover {
  color: #E27B58;
}
.showcase nav {
  margin-top: 3rem;
  margin-bottom: 3rem;
}
.showcase .spinner {
  margin: 5rem auto;
  text-align: center;
}
.showcase .spinner svg {
  height: 6rem;
  width: 6rem;
  fill: #E27B58;
  animation: rotate 2s infinite linear;
}
@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

.spinner {
  margin: 5rem auto;
  text-align: center;
}
.spinner svg {
  height: 6rem;
  width: 6rem;
  fill: #E27B58;
  animation: rotate 2s infinite linear;
}

